<script>
    export let content;
    export let title;
    let component;
    let scroll;
    let inView = false;
    let animation = false;
    $: inView = scroll && component && (component.getBoundingClientRect().top - window.innerHeight / 2) < 0;
    $: if (inView) {
        animation = true;
    }
</script>

<svelte:window bind:scrollY={scroll}></svelte:window>

<div bind:this={component} id="Projects" class="center">
    <h1  class="{animation ? "fade-in" : ""}">{title}</h1>
    <div class="container">
        {#each content as section, index}
            <div
                style="--delay: {0.25 * (1 + index)}s"
                class="shadow projects {animation ? "fade-in" : ""}">
                <div class="image">
                    <img src="images/{section.Image}" alt={section.Title} />
                </div>
                <div class="content">
                    <h2>{section.Title}</h2>
                    {#each section.Content as paragraph}
                        <p>{paragraph}</p>
                    {/each}
                    <div class="tags">
                        {#each section.Tags as tag}
                            <p>{tag}</p>
                        {/each}
                    </div>
                </div>
            </div>
        {/each}
    </div>
</div>

<style>
    @media screen and (max-width: 800px) {
        .projects {
            width: 100%!important;
        }
    }

    h1 {
        width: 100%;
        opacity: 0;
        margin: 20px 0;
        margin-top: 70px;
        text-align: center;
    }

    .container {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .projects {
        opacity: 0;
        width: 45%;
        margin: 20px 0;
        border-radius: 2px;
        background-color: var(--color-white);
    }

    .projects .image {
        width: 100%;
        margin: 0;
        border-top-left-radius: 2px;
        border-top-right-radius: 2px;
    }

    .projects .image img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        border-top-left-radius: 2px;
        border-top-right-radius: 2px;
    }

    .projects .content {
        padding: 10px;
        padding-bottom: 25px;
    }

    .projects .content h2, 
    .projects .content p {
        color: var(--color-background);
    }

    .projects .content .tags {
        position: absolute;
        bottom: 0;
    }

    .projects .content h2 {
        margin-top: 0;
    }

    .fade-in {
        --delay: 0.25s;
        animation: fadeInFromBelow 1s forwards var(--delay);
    }
</style>